.header {
    width: 100%;
    text-align: center;
    background-color: #84A178;
}
.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #84A178;
    margin: auto;
    width: 100%;

    
    @media screen and (min-width:1200px) {
        padding-top: 0;
    }
}
.logo {
    margin-right: auto;
    outline: 0;
        border: 0;
        @media screen and (max-width:767px) {
            padding-top: 12px;
            padding-bottom: 12px;
        }
        @media screen and (min-width:768px) {
            padding-top: 20px;
                padding-bottom: 20px;
        }
}

.header-logo  {
    fill:#F8D24E;
    

    @media screen and (min-width:320px) {
        width: 40px;
        height: 37px;
        margin-right: auto;
    }

    @media screen and (min-width:768px) {
        width: 48px;
        height: 44px;
                   }
    @media screen and (min-width:1200px) {
        width: 60px;
        height: 65px;
        margin-right: 269px;
    }

}


.menu-button {
    padding: 0;
    @media screen and (min-width:320px) {
        margin-top: 0;
    }
        @media screen and (min-width:768px), (max-width:1199px) {
        display: flex;
    }

    @media screen and (min-width:1200px) {
        display: none;
    }
    }

.nav__group {
    @media screen and (max-width:1199px) {
        display: none;
    }
    @media screen and (min-width:1200px) {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-right: auto;

    }
        
}

.nav__list {
display: flex;
gap: 27px;

}


.nav__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.25;
    color: #FCFCFC;
    outline: 0;

    @media screen and (min-width:320px) {
        &:hover {
                color: #F8D24E;
            }
        
            &:focus {
                color: #0A8817;
            }
        }
           
    @media screen and (min-width:1200px) {
        display: block;
        padding-top: 40px;
        padding-bottom: 40px;
    &:hover {
    color: #F8D24E;
    }
    &:focus {
    color: #0A8817;
    }
}

}

.button-header {
    @media screen and (max-width:767px) {
        display: none;
    }
    background-color: #F8D24E;
    width: 152px;
    height: 42px;
    text-align: center;
    display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        gap: 11px;
        cursor: pointer;
        outline: 0;
        border: 0;
        
        

        @media screen and (min-width:768px) {
            margin-left: 32px;
        }

        &:hover {
            background-color: #0A8817;
            color: #000000;
        }

        &:focus {
            background-color: #0A8817;
            color: #FCFCFC;
        }

        &:disabled {
            background-color: #ABABAB;
            color: #000000;

        }

    }

.text-button {
    color: #131313;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
}

.header-icon {
 width: 12px;
 height: 12px;
vertical-align: middle;
}


