.gallary {
   padding-bottom: 80px;
   margin-bottom: 0;
   background-image: 
   linear-gradient(to bottom,$bg-green 18%,rgba(245, 245, 245, 0) 18%);
@media screen and (min-width: 768px) {
    padding-bottom: 55px;

background-image:
    linear-gradient(to bottom, $bg-green 50%, rgba(245, 245, 245, 0) 50%);
} 
@media screen and (min-width: 1200px) {
    padding-bottom: 128px;
    background-image:
    linear-gradient(to bottom, $bg-green 22%, rgba(245, 245, 245, 0) 22%);
}
}

