.gallary__img {
    display: block;
    object-fit: cover;
@media screen and (max-width: 767px) {
            height: 279px;
            width: 100%;
    }
@media screen and (min-width: 768px) {
        height: 327px;
        width: 100%;
    }
@media screen and (min-width: 1199px) {
        height: 422px;
        width: 100%;
    }   
}

