.about {
    background-color: $bg-green; 
    padding-top: 60px;
    padding-bottom: 42px;

    @media screen and (min-width: 768px) {
    padding-bottom: 74px;
    }

    @media screen and (min-width: 1200px) {
    padding-bottom: 99px;
    }
}

.about__uppertitle {
    font-weight: 700;
    font-size: 18px;
    line-height: calc(22/18);
    text-align: center;
    text-transform: uppercase;
    color: $white-font-cl;
    margin-bottom: 12px;

    @media screen and (min-width: 768px) {
    font-weight: 800;
    font-size: 22px;
    line-height: calc(27/22);
    } 

    @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: calc(37/30);
    } 

}

.about__title {
font-family: $font-second;
font-size: 30px;
line-height: calc(38/30);
text-align: center;
text-transform: uppercase;
color: $white-font-cl;
margin-bottom: 32px;

    @media screen and (min-width: 768px) {
    font-size: 34px;
    line-height: calc(43/34);
    letter-spacing: 0.06em;
    margin-bottom: 72px;
    } 

    @media screen and (min-width: 1200px) {
    font-size: 36px;
    line-height: calc(45/36);
    margin-bottom: 82px;
    } 
}

.about__parts {
    @media screen and (min-width:768px) {
        display: flex;
        gap: 32px;
        justify-content: center;
        align-items: center;
    }

 @media screen and (min-width:1200px) {
    gap: 100px;
 }

}

.about__left {
position: relative;
width: 280px;
height: 280px;
background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
border-radius: 140.182px;
margin-bottom: 62px;
margin-left: auto;
margin-right: auto;

@media screen and (min-width: 768px) {
width: 335px;
height: 330px;
background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
box-shadow: 0px 5.30813px 19.9055px rgba(153, 114, 23, 0.25);
border-radius: 165.879px;
margin-bottom: 0;
margin-left: 0;
margin-right: 0;
}

@media screen and (min-width: 1200px) {
 width: 500px;
 height: 500px;   
 background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
border-radius: 250px;
}
}

.about__picture {
position: absolute;
top: 22px;
 border-radius: 50%;  
 overflow: hidden;

 @media screen and (min-width:768px) {
   top: 26px; 
 }

 @media screen and (min-width:1200px) {
  top: 39px;
  width: 490px;
  height: 490px;  
 }
} 


.about__right {
    margin-left: 30px;
    margin-right: 30px;
  
    @media screen and (min-width:768px) {
      margin-left: 0;  
      margin-right: 0;
      width: 336px;
    }

    @media screen and (min-width: 1200px) {
       width: 463px; 
    }
}

.about__desc {
font-size: 12px;
line-height: calc(21/12);
text-align: center;
color: $white-font-cl;

&:nth-child(1) {
    margin-bottom: 4px;
}

&:nth-child(2) {
    margin-bottom: 4px;
}

&:nth-child(3) {
    margin-bottom: 20px;
}
}

.about__desc {
@media screen and (min-width: 768px) {
text-align: left;
    
&:nth-child(1) {
        margin-bottom: 8px;
    }

    &:nth-child(2) {
        margin-bottom: 8px;
    }

    &:nth-child(3) {
        margin-bottom: 48px;
    }
}
}


.about__desc {
 @media screen and (min-width: 1200px) {
font-size: 16px;
line-height: calc(28/16); 
}
}

.about__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    margin-left: auto;
    margin-right: auto;
    background-color: $bg-yellow-cl;
    font-weight: 600;
    line-height: calc(18/14);
    transition: background-color $animation, color $animation;
    

    &:hover {
        background-color: $btn-cl-green;
    }

    &:focus {
        background-color: $btn-cl-green;
        color: $white-font-cl;
    }   

    &:disabled {
        background-color: $btn-cl-gray;
    }

    @media screen and (min-width: 768px) {
        margin-left: 0;
    }
}

.about__icon {
    fill: $black-font-cl;
    transition: fill $animation;

    .about__btn:focus & {
    fill: $white-font-cl;
    }
}